@font-face {
    font-family: Fjalla One;
    src: url("../fonts/FjallaOne-Regular.ttf");
}

*{
    box-sizing: border-box;
}

.main{
    display: flex;
    flex-direction: column;
    height: 100%;
}

.header{
    display: flex;
    height: 10%;
    align-items: center;
    justify-content: space-between;
    background-color: aquamarine;
}

.header h1{
    margin-left: 50px;
    font-family: Fjalla One;
    font-size: 36px;
    animation-name: slide;
    animation-duration: 1s;
}         

.subHeader{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 24px;
    margin-top: 12px;
    font-style: inherit;
    color: black;
    height: 10%;
}

.conteudo{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60%;
}

footer{
    height: 20%;
    border: 2px rgba(169, 169, 169, 0.5) solid;
    background-color: rgba(128, 128, 128, 0.1);
}

footer h1{
    margin: 2px;
}

/*renderData*/

.modal{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    width: 300px;
    height: 200px;
    background-color: rgba(127, 255, 212, 0.609);
}

.modal input{
    width: 172px;
    margin-top: 2px;
     
}

.modal button{
    width: 180px;
}

.imagemNFT{
    height: 90%;
}

.list{
    display: flex;
    flex-direction: column;
}

.renderDataWithProp{
    display: flex;
    margin-top: 16px;
    border: 1px solid;
}

.renderDataWithProp-left{
    display: flex;
    align-items: center;
}

.renderDataWithProp-left h1{
    font-size: 20px;
    flex: 80%;
}

.renderDataWithProp-right{
    text-align: center;
    max-height: 200px;
    flex: 20%;
}

.imagemAddressPreview{
    height: 200px;
}

@media screen and (max-width:1000px) {
    .renderDataWithProp{
        display: flex;
        flex-direction: column;
    }
}

@keyframes slide{
    0%{
        transform:translateX(-190px)
    }
    100%{
        transform:translateX(0px)
    }
}
